.ftf-resource__header {
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  color: #fff;
}
.ftf-resource__header:after {
  content: "";
  position: absolute;
  background-color: rgba(0, 0, 0, 0.5);
  inset: 0;
  z-index: 0;
}
.ftf-resource__header .container {
  position: relative;
  z-index: 1;
}
.ftf-resource__header .ftf-avatar {
  text-align: left;
}
.ftf-resource__header .ftf-avatar__name a {
  color: #fff;
  opacity: 0.6;
}
.ftf-resource__title {
  color: #fff;
}